<template>

 <v-container fluid>
      <v-row dense>
        <v-col
          v-for="card in cards"
          :key="card.nombre"
          :cols="4"
        >
          <v-card>
            <v-img
              :src="`//127.0.0.1:8000/imagenes/${card.imagen}`"
              class="white--text align-end"
              gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
              height="200px"
            >
              <v-card-title v-text="card.nombre"></v-card-title>
            </v-img>

            <v-card-actions>
              <v-spacer></v-spacer>

              <v-btn icon>
                <v-icon>mdi-heart</v-icon>
              </v-btn>

              <v-btn icon>
                <v-icon>mdi-bookmark</v-icon>
              </v-btn>

              <v-btn icon>
                <v-icon>mdi-share-variant</v-icon>
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
      {{ cards }}
    </v-container>

</template>

<script>
import axios from 'axios';

  export default {
    data(){
      return {
        cards: [],

      }
    },
    async mounted(){
      let {data} = await axios.get("//127.0.0.1:8000/api/inicio_pagina");
      this.cards = data
    }
  }
</script>
